@import url("https://cdn.jsdelivr.net/gh/orioncactus/pretendard@v1.3.8/dist/web/static/pretendard.css");

body {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #f6f6f6;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-family: 'pretendard';
  font-weight: 400;
}

* {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

@media screen and (min-width: 768px) {
  .App {
    width: 640px;
  }
}

@media screen and (max-width: 768px) {
  .App {
    width: 90vw;
  }
}

#root {
  background-color: #fff;
  box-shadow: rgba(100,100,111,.2) 0 7px 29px 0;
}

.App {
  min-height: 100vh;
}

.btn {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  background-color: #ececec;
  font-family: 'pretendard';
  font-size: 18px;
  line-height: 22px;
  color: #000;
  cursor: pointer;
}

.btn_positive {
  background-color: #64c964;
  color: #fff;
}

.btn_negative {
  background-color: #fd565f;
  color: #fff;
}

.header {
  display: flex;
  align-items: center;
  padding: 20px;
  border-bottom: 1px solid #e2e2e2;
}

.header_title {
  display: flex;
  flex-grow: 1;
  justify-content: center;
  font-size: 1.8em;
  font-weight: 700;
}

.header_title:last-child {
  padding-right: 51px;
}

.filterBar {
  display: flex;
  padding: 10px 20px;
}

.filterBar .btn {
  flex-grow: 1;
}

.controlMenu {
  margin-left: 10px;
  padding: 5px;
  border: none;
  background-color: #ececec;
  border-radius: 5px;
}

.controlMenu:first-child {
  margin-left: 0;
}

.controlMenu + .btn {
  margin-left: 20px;
}

.diaryList .list_content {
  padding: 0 20px;
}

.diaryItem {
  display: flex;
  border-bottom: 1px solid #e2e2e2;
}

.diaryItem .img_box {
  display: flex;
  justify-content: center;
  min-width: 120px;
  height: 80px;
  border-radius: 5px;
}

.diaryItem .img_box.emotion1 {background-color: #64c964;}
.diaryItem .img_box.emotion2 {background-color: #9dd772;}
.diaryItem .img_box.emotion3 {background-color: #fdce17;}
.diaryItem .img_box.emotion4 {background-color: #fd8446;}
.diaryItem .img_box.emotion5 {background-color: #fd565f;}

.diaryItem .info_box {
  display: flex;
  flex-grow: 1;
  padding: 15px 0;
  color: #111;
  text-decoration: none;
}
.diaryItem .data_box {
  flex-grow: 1;
  margin-left: 10px;
}

.diaryItem .date {
  display: block;
  margin-bottom: 10px;
  font-weight: 700;
}

.diaryItem .btn_box {
  display: flex;
  align-items: center;
}

.diaryEditor .content {
  padding: 20px;
}

.diaryEditor .title {
  display: block;
  margin-top: 40px;
  margin-bottom: 20px;
  font-size: 18px;
  font-weight: 700;
}

.diaryEditor .title:first-of-type {
  margin-top: 0;
}

.diaryEditor .input_date {
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  background-color: #ececec;
  font-size: 15px;
}

.diaryEditor .list_emotion {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -5px;
}

.list_emotion li {
  flex-basis: calc(20% - 10px);
  margin: 0 5px;
}

.list_emotion .btn_emotion {
  display: block;
  padding: 15px;
  border: none;
  border-radius: 5px;
  background-color: #ececec;
  cursor: pointer;
}

.list_emotion .img_box {
  display: block;
  margin-bottom: 15px;
  padding: 8px;
}

.list_emotion .img_box img {
  display: block;
  width: 100%;
}

.list_emotion .text {
  font-size: 13px;
  font-weight: 500;
}

.btn_emotion.is_selected_1 {background-color:#64c964;color:#fff}
.btn_emotion.is_selected_2 {background-color:#9dd772;color:#fff}
.btn_emotion.is_selected_3 {background-color:#fdce17;color:#fff}
.btn_emotion.is_selected_4 {background-color:#fd8446;color:#fff}
.btn_emotion.is_selected_5 {background-color:#fd565f;color:#fff}

.diaryEditor .textarea {
  display: block;
  width: 100%;
  min-height: 200px;
  padding: 20px;
  box-sizing: border-box;
  border: none;
  border-radius: 5px;
  background-color: #ececec;
  resize: none;
}

.diaryEditor .btn_box {
  display: flex;
  margin-top: 50px;
}

.diaryEditor .btn_box .btn:last-child {
  margin-left: auto;
}

.diaryView .content {
  padding: 20px;
}

.diaryView .content .title {
  margin-top: 60px;
  margin-bottom: 30px;
  font-size: 20px;
  font-weight: 700;
  text-align: center;
}

.diaryView .content .title:first-child {
  margin-top: 20px;
}

.diaryView .img_box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 250px;
  height: 250px;
  margin: 0 auto;
  padding: 20px;
  box-sizing: border-box;
  border-radius: 5px;
  background-color: #ececec;
}

.diaryView .img_box img {
  display: block;
  margin: 0 auto;
}

.diaryView .img_box .desc {
  display: block;
  margin-top: auto;
  margin-bottom: 15px;
  font-size: 17px;
  font-weight: 600;
  color:#fff;
  text-align: center;
}

.diaryView .img_box.emotion1 {background-color:#64c964}
.diaryView .img_box.emotion2 {background-color:#9dd772}
.diaryView .img_box.emotion3 {background-color:#fdce17}
.diaryView .img_box.emotion4 {background-color:#fd8446}
.diaryView .img_box.emotion5 {background-color:#fd565f}

.diaryView .text {
  padding: 20px;
  background-color: #ececec;
  border-radius: 5px;
  font-size: 16px;
  line-height: 22px;
  word-break: break-all;
}